.flex-container {
  display: flex;
  flex-direction: row;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: top;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Nav-div {
  background-color: #28ff34;
  width: 100%;
}

.Nav-link {
  color: #0900ac;
  margin: 20px;
}

